import React, { Fragment } from "react";
import Grid from "@material-ui/core/Grid";
import { Typography } from "@material-ui/core";
import Avatar from "@material-ui/core/Avatar";
import { makeStyles } from "@material-ui/core/styles";
import Chip from "@material-ui/core/Chip";
import Divider from "@material-ui/core/Divider";
import IconButton from "@material-ui/core/IconButton";
import InstagramIcon from "@material-ui/icons/Instagram";
import EmailIcon from "@material-ui/icons/Email";
import LinkedInIcon from "@material-ui/icons/LinkedIn";
import Grow from "@material-ui/core/Grow";
import Typewriter from "typewriter-effect";

const useStyles = makeStyles((theme) => ({
  root: {},
  avatarContainer: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
    [theme.breakpoints.only("xs")]: {
      marginBottom: "1.875em",
    },
  },
  chipContainer: {
    margin: theme.spacing(1),
    textAlign: "center",
  },
  typographyContainer: {
    textAlign: "center",
  },
  typographyText: {
    "&::selection": {
      color: "white",
      background: "#FC4A37",
    },
    "&::-moz-selection": {
      color: "white",
      background: "#FC4A37",
    },
  },
  nameContainer: {
    display: "flex",
    justifyContent: "center",
    alignItems: "center",
  },
  iconContainer: {
    margin: theme.spacing(1),
    color: "black",
  },
  contactContainer: {
    textAlign: "center",
  },
  avatarImage: {
    width: "18.75rem",
    height: "18.75rem",
    boxShadow: `12px 12px 24px 0 rgba(0, 0, 0, 0.1),
    -12px -12px 24px 0 rgba(255, 255, 255, 3)`,
  },
  skillText: {
    fontWeight: 600,
  },
}));

const skillImage = {
  TYPESCRIPT: require("../../assets/skillSvg/typescript.svg"),
  JAVASCRIPT: require("../../assets/skillSvg/javascript.svg"),
  HTML: require("../../assets/skillSvg/html5.svg"),
  CSS: require("../../assets/skillSvg/css3.svg"),
  REACT: require("../../assets/skillSvg/reactjs.svg"),
  REDUX: require("../../assets/skillSvg/redux.svg"),
  MATERIALUI: require("../../assets/skillSvg/materialui.svg"),
  GRAPHQL: require("../../assets/skillSvg/graphql.svg"),
  NODEJS: require("../../assets/skillSvg/nodejs.svg"),
  MONGODB: require("../../assets/skillSvg/mongodb.svg"),
  POSTGRESQL: require("../../assets/skillSvg/sql.svg"),
  REDIS: require("../../assets/skillSvg/redis.svg"),
  GIT: require("../../assets/skillSvg/git.svg"),
  DOCKER: require("../../assets/skillSvg/docker.svg"),
  KUBERNETES: require("../../assets/skillSvg/kubernetes.png"),
};

const About = () => {
  const classes = useStyles();
  const [checked, setChecked] = React.useState(false);

  React.useEffect(() => {
    setChecked((prev) => !prev);
    document.title = "Portfolio/About";
  }, []);

  return (
    <Fragment>
      <Grid container className={classes.root}>
        <Grow
          in={checked}
          style={{ transformOrigin: "0 0 0" }}
          {...(checked ? { timeout: 1000 } : {})}
        >
          <Grid item xs={12} sm={6} className={classes.avatarContainer}>
            <Avatar
              src={require("../../assets/profileImage.svg")}
              className={classes.avatarImage}
            />
          </Grid>
        </Grow>

        <Grow
          in={checked}
          style={{ transformOrigin: "0 0 0" }}
          {...(checked ? { timeout: 1000 } : {})}
        >
          <Grid item xs={12} sm={6}>
            <Grid container direction="column">
              <Grid
                item
                // style={{ backgroundColor: "#48C9B0" }}
                className={classes.nameContainer}
              >
                <Typography variant="h2">
                  <Typewriter
                    options={{
                      strings: ["Prashant", "Hello! Nice to meet you."],
                      autoStart: true,
                      loop: true,
                      cursor: "",
                      delay: 50,
                    }}
                  />
                </Typography>
              </Grid>

              <Grid
                item
                //style={{ backgroundColor: "#AF7AC5" }}
                className={classes.chipContainer}
              >
                {[
                  "TYPESCRIPT",
                  "JAVASCRIPT",
                  "HTML",
                  "CSS",
                  "REACT",
                  "REDUX",
                  "MATERIALUI",
                  "GRAPHQL",
                  "NODEJS",
                  "MONGODB",
                  "POSTGRESQL",
                  "REDIS",
                  "GIT",
                  "DOCKER",
                  "KUBERNETES",
                ].map((name, index) => (
                  <Chip
                    avatar={<Avatar alt="Natacha" src={skillImage[name]} />}
                    className={classes.skillText}
                    label={name}
                    key={index}
                    style={{ marginRight: "10px", marginBottom: "4px" }}
                  />
                ))}
              </Grid>
              <Grid
                item
                //style={{ backgroundColor: "#EC7063" }}
                className={classes.typographyContainer}
              >
                <Typography variant="body1" className={classes.typographyText}>
                  I'm an avid follower of technology, a developer and programmer
                  who loves learning new tech skills and I'm always looking for
                  something new to challenge myself.I passionate about
                  developing products that have positive real life impact. As a
                  javascript junkie i have worked on MERN stack and with several
                  other technologies, while keeping a holistic perspective in
                  mind.
                </Typography>
              </Grid>
              <Divider style={{ marginTop: "10px" }} />
              <Grid item className={classes.contactContainer}>
                <IconButton
                  aria-label="Instgram"
                  className={classes.iconContainer}
                  href="https://www.instagram.com/prashantjdreww"
                  target="_blank"
                >
                  <InstagramIcon fontSize="large" />
                </IconButton>
                <IconButton
                  aria-label="Email"
                  className={classes.iconContainer}
                  href="mailto:prashantdrew23@gmail.com"
                  target="_blank"
                >
                  <EmailIcon fontSize="large" />
                </IconButton>
                <IconButton
                  aria-label="LinkedIn"
                  className={classes.iconContainer}
                  href="https://www.linkedin.com/in/prashantkumar23"
                  target="_blank"
                >
                  <LinkedInIcon fontSize="large" />
                </IconButton>
              </Grid>
            </Grid>
          </Grid>
        </Grow>
      </Grid>
    </Fragment>
  );
};

export default About;
