import React, { Fragment } from "react";
import cx from "clsx";
import { makeStyles } from "@material-ui/core/styles";
import Card from "@material-ui/core/Card";
import CardMedia from "@material-ui/core/CardMedia";
import CardContent from "@material-ui/core/CardContent";
import CardActions from "@material-ui/core/CardActions";
import Button from "@material-ui/core/Button";
import Dialog from "@material-ui/core/Dialog";
import DialogTitle from "@material-ui/core/DialogTitle";
import DialogContent from "@material-ui/core/DialogContent";
import DialogActions from "@material-ui/core/DialogActions";
import Typography from "@material-ui/core/Typography";
import TextInfoContent from "@mui-treasury/components/content/textInfo";
import { useFourThreeCardMediaStyles } from "@mui-treasury/styles/cardMedia/fourThree";
import { useN04TextInfoContentStyles } from "@mui-treasury/styles/textInfoContent/n04";

const useStyles = makeStyles(() => ({
  root: {
    maxWidth: 343,
    margin: "auto",
    borderRadius: 30,
    padding: 12,
    backgroundColor: "#ECF0F1",
    boxShadow: `12px 12px 16px 0 rgba(0, 0, 0, 0.1),
    -4px -4px 12px 0 rgba(253, 254, 254 , 1)`,
  },
  media: {
    borderRadius: 6,
  },
  dialogContainer: {
    backgroundColor: "#ECF0F1",
    "&::-webkit-scrollbar-track": {
      backgroundColor: "#ECF0F1",
    },
    "&::-webkit-scrollbar": {
      width: "12px",
      backgroundColor: "#ECF0F1",
    },

    "&::-webkit-scrollbar-thumb": {
      borderRadius: "10px",
      backgroundColor: "#555",
    },
  },
}));

export const CardDemoMemozied = React.memo(function CardDemo({
  TechStack,
  ProjectName,
  ProjectImage,
  About,
  DemoLink,
  CodeLink,
}) {
  const styles = useStyles();
  const mediaStyles = useFourThreeCardMediaStyles();
  const textCardContentStyles = useN04TextInfoContentStyles();

  const [open, setOpen] = React.useState(false);

  const handleClickOpen = () => {
    setOpen(true);
  };
  const handleClose = () => {
    setOpen(false);
  };

  return (
    <Fragment>
      <Card className={cx(styles.root)} elevation={0}>
        <CardMedia
          className={cx(styles.media, mediaStyles.root)}
          style={{ borderRadius: 20 }}
          image={ProjectImage}
        />
        <CardContent>
          <TextInfoContent
            classes={textCardContentStyles}
            overline={TechStack}
            heading={ProjectName}
          />
        </CardContent>
        <CardActions
          disableSpacing
          style={{ display: "flex", justifyContent: "center" }}
        >
          <Button
            aria-label="readmore"
            style={{ marginRight: "10px", fontWeight: 600 }}
            onClick={handleClickOpen}
          >
            About
          </Button>
          <Button
            aria-label="demo"
            href={DemoLink}
            target="__blank"
            style={{ fontWeight: 600 }}
          >
            Demo
          </Button>
          <Button
            aria-label="demo"
            href={CodeLink}
            target="__blank"
            style={{ fontWeight: 600 }}
          >
            Code
          </Button>
        </CardActions>
      </Card>
      <Dialog
        onClose={handleClose}
        aria-labelledby="customized-dialog-title"
        open={open}
      >
        <DialogTitle
          id="customized-dialog-title"
          onClose={handleClose}
          className={styles.dialogContainer}
        >
          Project Title
        </DialogTitle>
        <DialogContent dividers className={styles.dialogContainer}>
          <Typography gutterBottom variant="h6">
            {About[0]}
          </Typography>
          <Typography gutterBottom variant="h6">
            {About[1]}
          </Typography>
          <Typography gutterBottom variant="h6">
            {About[2]}
          </Typography>
        </DialogContent>
        <DialogActions className={styles.dialogContainer}>
          <Button autoFocus onClick={handleClose} color="primary">
            Okay
          </Button>
        </DialogActions>
      </Dialog>
    </Fragment>
  );
});
export default CardDemoMemozied;
