import React from "react";
import styled from "styled-components";
import { makeStyles } from "@material-ui/core/styles";
import { createMuiTheme, responsiveFontSizes } from "@material-ui/core/styles";
import CssBaseline from "@material-ui/core/CssBaseline";
import Toolbar from "@material-ui/core/Toolbar";
import Typography from "@material-ui/core/Typography";
import Grid from "@material-ui/core/Grid";
import {
  Root,
  getHeader,
  getDrawerSidebar,
  getSidebarTrigger,
  getSidebarContent,
  getCollapseBtn,
  getContent,
  getStandardScheme,
  getFooter,
} from "@mui-treasury/layout";
import ListItem from "@material-ui/core/ListItem";
import ListItemIcon from "@material-ui/core/ListItemIcon";
import ListItemText from "@material-ui/core/ListItemText";
import List from "@material-ui/core/List";
import { Switch, Link, Route } from "react-router-dom";
import About from "./components/About";
import Project from "./components/Project";
import UserIcon from "./assets/user.svg";
import ProjectIcon from "./assets/project.svg";

const Header = getHeader(styled);
const DrawerSidebar = getDrawerSidebar(styled);
const SidebarTrigger = getSidebarTrigger(styled);
const SidebarContent = getSidebarContent(styled);
const CollapseBtn = getCollapseBtn(styled);
const Content = getContent(styled);
const Footer = getFooter(styled);

const standardScheme = getStandardScheme();

let theme = createMuiTheme({
  palette: {
    primary: {
      main: "#000",
    },
    secondary: {
      main: "#ECF0F1",
    },
  },
  overrides: {
    MuiListItem: {
      root: {
        "&.Mui-selected": {
          backgroundColor: "#7d4709",
          color: "#fff",
          "& svg": {
            color: "#fff",
          },
          "&:hover": {
            backgroundColor: "#95550b",
          },
        },
      },
    },
    MuiListItemIcon: {
      root: {
        minWidth: 48,
      },
    },
  },
  typography: {
    h3: {
      fontSize: 40,
      fontFamily: "'Montserrat', sans-serif",
    },
    h2: {
      fontFamily: "'Niconne', cursive",
    },
    body1: {
      fontFamily: "'Forum', cursive",
      fontSize: "1.3rem",
    },
    h4: {
      fontFamily: "'Fredoka One', cursive",
    },
  },
});

theme = responsiveFontSizes(theme);

const useStyles = makeStyles((theme) => ({
  textContainer: {
    color: "black",
    textDecoration: "none",
    "&:active": {
      color: "#7d4709",
    },
  },
  headingContainer: {
    textAlign: "center",
  },
  drawerContainer: {
    marginTop: "100px",
    backgroundColor: "#ECF0F1",
  },
  linkContainer: {
    textDecoration: "none",
    color: "black",
  },
}));

const CustomThemeDemo = () => {
  // by default, Root will render ThemeProvider, pass "theme" to override default theme
  // If you want to use your own ThemeProvider, pass "themeProvidedOmitted" to Root and wrapped it with your ThemeProvider
  // note: If you set "themeProvidedOmitted=true" to Root without your own ThemeProvider, you will see error "cannot read palette of undefined"
  const classes = useStyles();

  return (
    <Root theme={theme} scheme={standardScheme}>
      <CssBaseline />
      <Header color={"secondary"}>
        <Toolbar>
          <SidebarTrigger
            sidebarId={"primarySidebar"}
            style={{ color: "black" }}
          />
          <Grid container justify="center">
            <Grid item xs={12} className={classes.headingContainer}>
              <Typography variant="h4">
                <Link to="/" className={classes.linkContainer}>
                  Portfolio
                </Link>
              </Typography>
            </Grid>
          </Grid>
        </Toolbar>
      </Header>
      <DrawerSidebar
        sidebarId={"primarySidebar"}
        style={{ backgroundColor: "#ECF0F1" }}
      >
        <SidebarContent
          style={{ backgroundColor: "#ECF0F1", overflow: "hidden" }}
        >
          <List>
            <Link to="/" className={classes.textContainer}>
              <ListItem>
                <ListItemIcon>
                  <img
                    src={UserIcon}
                    alt=""
                    style={{ width: "20px", height: "20px" }}
                  />
                </ListItemIcon>
                <ListItemText>About</ListItemText>
              </ListItem>
            </Link>
            <Link to="/project" className={classes.textContainer}>
              <ListItem>
                <ListItemIcon>
                  <img
                    src={ProjectIcon}
                    alt=""
                    style={{ width: "20px", height: "20px" }}
                  />
                </ListItemIcon>
                <ListItemText>Project</ListItemText>
              </ListItem>
            </Link>
          </List>
        </SidebarContent>
        <CollapseBtn style={{ backgroundColor: "#ECF0F1" }} />
      </DrawerSidebar>
      <div
        style={{ marginLeft: "20px", marginTop: "60px", marginRight: "20px" }}
      >
        <Content>
          <Switch>
            <Route exact path="/">
              <About />
            </Route>
            <Route exact path="/project">
              <Project />
            </Route>
          </Switch>
        </Content>
      </div>
      <Footer
        style={{
          display: "flex",
          justifyContent: "center",
          bottom: 0,
          marginTop: "20px",
        }}
      >
        <Typography>Copywright &#169; 2020</Typography>
      </Footer>
    </Root>
  );
};

export default CustomThemeDemo;
